import {
  Component,
  OnInit,
  Input,
  AfterViewChecked,
  ElementRef,
  Output,
  EventEmitter,
  QueryList,
  AfterViewInit,
  ContentChildren,
} from '@angular/core';
import { BcSearchBehavior } from '@bancolombia/design-system-behaviors';
import { BcInputDateComponent } from '@bancolombia/design-system-web/bc-input-date';
import { BcSearchAdvancedItemComponent } from './bc-search-item-advanced/bc-search-item-advanced.component';
import { BcHelperService } from '@bancolombia/design-system-web/bc-helpers';

interface ItemSearch {
  label: string;
  value: string;
}

interface ContentSelect {
  nameSearch: string;
  value?: any;
  typeSelect?: any;
  items: ItemSearch[];
}

interface DataSelect {
  [id: string]: ContentSelect;
}
/**
 * Componente busqueda
 *
 * @export
 * @class BcSearchComponent
 * @implements {OnInit}
 * @implements {AfterViewChecked}
 * @implements {AfterViewInit}
 */
@Component({
  selector: 'bc-search',
  templateUrl: './bc-search.component.html',
})
export class BcSearchComponent implements OnInit, AfterViewChecked, AfterViewInit {
  @ContentChildren(BcSearchAdvancedItemComponent, { descendants: true })
  advanceItems: QueryList<BcSearchAdvancedItemComponent>;

  @ContentChildren(BcInputDateComponent, { descendants: true })
  inputsDate: QueryList<BcInputDateComponent>;


  /**
 * Id del elemento.
 *
 * @type {string}
 * @memberOf BcPaginatorComponent
 */
  @Input() componentId: string = this.bcHelperService.getId(this);


  /**
   * items del componente search a buscar.
   *
   * @type {array}
   * @memberof BcSearchComponent
   */

  @Input() items = [];


  /**
   * Items del componente select que van a ser renderizados, DEBE ser de tipo DataSelect y el nombre de
   * la propieda debe ser igual al ID del
   * input select el cual debe ser un objeto que DEBE tener los datos
   * nameSearch: string; value?: any; typeSelect?: any; items: ItemSearch[];
   * @type {('DataSelect')}
   * @memberof BcSearchComponent
   */
  @Input() itemsSelect: DataSelect;

  /**
     * Funcion que se ejecuta una promesa de manera sincrona antes de ejecutar la busqueda de elementos  
     * @type {('Funcion(Promise(object))')}
     * @memberof BcSearchComponent
     */
  @Input() preSearchFunction: (data) => Promise<object>;

  /**
   * Evento que emite los Objetos filtrados para realizar busquedas externas  .
   *
   * @type {string}
   * @memberof BcSearchComponent
   */

  @Output() objFilter = new EventEmitter();

  /**
   * Evento que emite evento cuando se limpian los filtros.
   *
   * @type {CustomEvent}
   * @memberof BcSearchComponent
   */

  @Output() clearEvent = new EventEmitter();

  bcSearchBehavior;

  constructor(private elRef: ElementRef, private bcHelperService: BcHelperService) {
    this.bcSearchBehavior = new BcSearchBehavior();
  }

  ngOnInit() {
    this.bcSearchBehavior.setElement(this.elRef.nativeElement);
    this.bcSearchBehavior.clearSearchOutput();
    this.bcSearchBehavior.outputFunctionClear = (data) => {
      this.clearEvent.emit(data);
    };
  }

  ngAfterViewChecked(): void {
    this.bcSearchBehavior.setItems(this.items);
    this.bcSearchBehavior.outputFunction = (data) => {
      this.objFilter.emit(data);
    };
    this.bcSearchBehavior.preSearchFunction = async (data) => {
      if (this.preSearchFunction) {
        await this.preSearchFunction(data)
      }
    };
    this.bcSearchBehavior.setInputsElementSelect(this.itemsSelect);
  }

  ngAfterViewInit() {
    this.bcSearchBehavior.setUp();
    this.advanceItems.forEach((item) => {
      item.valueSelected.subscribe((data) => {
        if (data) {
          this.bcSearchBehavior.searchObject();
        }
      });
    });
    this.inputsDate.forEach((element) => {
      element.updateValue.subscribe((value) => {
        this.bcSearchBehavior.searchObject();
      });
    });
  }
  /**
   * CLimpia los filtros del search.
   *
   * @memberof BcInputDateRangeComponent
  */
  clearSearch() {
    this.bcSearchBehavior.cleanValidationSearch();
  }
}
