import { Component, Input } from '@angular/core';

/**
 * Componente para el resultado de los filtros.
 *
 * @export
 * @class BcSearchResultFilterComponent
 */
@Component({
    selector: 'bc-search-result-filter',
    templateUrl: './bc-search-result-filter.component.html'
})
export class BcSearchResultFilterComponent {

    /**
        * Items para los li cuando no se encuentran resultados.
        *
        * @type {boolean}
        * @memberof BcSearchLeftComponent
    */
    @Input() itemsli: any[];

    /**
        * texto label elemento
        *
        * @type {boolean}
        * @memberof BcSearchLeftComponent
    */

    @Input() labelResultNull = '';

    /**
        * Imagen del elemento cuando no encuentra resultados.
        *
        * @type {boolean}
        * @memberof BcSearchLeftComponent
    */

    @Input() imageResultNull = '';
}
